import bildPortriat1 from '../Img/Animals1.jpg'
import bildPortriat2 from '../Img/Animals2.jpg'
import bildPortriat3 from '../Img/Animals3.jpg'
import styled from 'styled-components'
import device from '../Components/device'
import ScrollToTop from '../Components/ScrollToTop'
import {useHistory} from 'react-router-dom'
import {FaAngleLeft} from 'react-icons/fa'
import {useState} from 'react'



const Portrait = () => {
  const history =useHistory();
const [image, setImage] =useState([
  {
  src:bildPortriat1,
  id:1,
  active:false
  
},{
  src:bildPortriat2,
  id:2,
  active:false
},{src:bildPortriat3,
  id:3,
  active:false
} ])

  const BildDivWraper = styled.div`
  display:flex;
  flex-direction:column;
  align-items: center;
  justify-content:space-around;
  flex-wrap: no-wrap;
  height:280vh;
  width:100%;
  overflow:hidden;
  
  
`

  const ImageBox = styled.img` 
  display:flex;
  height:80vh;
  align-items: center;
  justify-content:center;
  
  @media ${device.mobile} {
    
 
      } 
      `
const BackArrow = styled(FaAngleLeft)`
         position:fixed;
         top:90vh;
         left:15%;
         transform:translate(-50%);
         width:90px;
         color:black;
         height:40px;
         cursor:pointer;
         
         `

  const handleClick =(id)=>{
setImage( image.map(item=>{
if(id=== item.id) {
  item.active = !item.active
}
return item}))

};
  


return (
    <>
  <ScrollToTop/>
    <BildDivWraper >
    { image.map(({id, src,active}) =>
    <ImageBox 
    style={{
      height:active?"100vh":"80vh",
      top:active?"50%":"",
      left:active?"50%":"",
      position:active?"fixed":"",
    transform:active?"translate(-50%,-50%)":"",}}
    key={id}
    id={id}
    src={src}
    active={active}
    onClick={()=>handleClick(id,active)}
    />
    )}
</BildDivWraper>
 <BackArrow onClick={()=>history.goBack()}/> 
  </>);
}

export default Portrait;