import styled from 'styled-components'
import bild1 from '../Img/1.jpg'
import bild2 from '../Img/2.jpg'
import bild3 from '../Img/3.jpg'
import bild4 from '../Img/4.JPG'
import {
    Switch,
    Route,
    Link,
 } from "react-router-dom";
import device from '../Components/device'
import ScrollToTop from '../Components/ScrollToTop'
import { CgArrowLongUpR } from "react-icons/cg";
import { useState} from 'react'


const BildBox1 = styled.div`
height: 100vh;
width: 100%;
background-image: url(${bild1});
background-repeat: no-repeat;
background-position: center;

@media ${device.mobile} {
   background-position: -250px center;
   background-size:cover ;
   background-attachment: fixed;
  }

    `
    const BildBox2 = styled.div`
height: 100vh;
width: 100%;
background-image: url(${bild2});
background-repeat: no-repeat;

@media ${device.mobile} {
   background-position:-300px center;
   background-size:cover ;
   background-attachment: fixed;
  }


    `
    const BildBox3 = styled.div`
    height: 100vh;
    width: 100%;
    background-image: url(${bild3});
    background-repeat: no-repeat;

@media ${device.mobile} {
   background-position:-300px center;
   background-size:cover ;
   background-attachment: fixed;
  }
    
        `
        const BildBox4 = styled.div`
    height: 100vh;
    width: 100%;
    background-image: url(${bild4});
    background-repeat: no-repeat;

@media ${device.mobile} {
   background-position:-300px center;
   background-size:cover ;
   background-attachment: fixed;
  }
        `

        const TextBild = styled.div`
        font-size:60px;
        text-Align:center;
        
        position:absolute;
        top:50%;
        left:50%;
        transform:translate(-50%,-50%);
        color:white;
        cursor:pointer;
        &:hover{opacity:0.7;}
        `
        
        const WrapLink = styled(Link)`
        color:white;
        text-shadow:-1px -1px 0 #000, 1px -1px 0 #000, -1px 1px 0 #000, 1px 1px 0 #000;

        &:hover{
            opacity:0.5;
            color:white;
            text-decoration:none;
        }
        `

        const BackArrow = styled(CgArrowLongUpR)`
        position:fixed;
        z-index:120;
        top:85vh;
        left:50%;
        transform:translate(-50%);
        width:100px;
        color:white;
        opacity:0.8;
        height:40px;
        text-shadow: 1px 1px 2px pink;
        transition: .5s linear;

    &:hover {
        height:70px;
        opacity:1;

    }

        `
        const BildBoxWrap = styled.div``
        
const Main=()=>{
    
 const [showScroll, setShowScroll] =useState(false)
    const checkScrollTop=()=>{
        if(!showScroll&&window.pageYOffset>400){
            setShowScroll(true)
        }else if (showScroll&& window.pageYOffset <=400){
            setShowScroll(false)
        }
    }
    window.addEventListener('scroll', checkScrollTop)
        const scrollTop = () =>{
            window.scrollTo({top: 0, behavior: 'smooth'});
         };
    
         return(
        
<div  >


<ScrollToTop/>



<BildBoxWrap >
<BildBox1  style={{zIndex:"25", position: "sticky", top:"-80vh", height:"120vh"}}><TextBild><WrapLink to="/portrait">-Portrait-</WrapLink></TextBild></BildBox1>

<BildBox2  style={{zIndex:"26", position:"sticky", top:"-50vh",backgroundPosition:"center",height:"100vh"}}><TextBild><WrapLink to="/animals">-Animals-</WrapLink></TextBild></BildBox2>

<BildBox3 style={{zIndex:"27", top:"-80vh", position:"sticky",backgroundPosition:"center",height:"100vh"}} ><TextBild><WrapLink to="/art">-Art-</WrapLink></TextBild></BildBox3>

<BildBox4  style={{zIndex:"28", position:"sticky", top:"-50vh",backgroundPosition:"center",height:"120vh"}}><TextBild><WrapLink to="/architecture">-Landscape-</WrapLink></TextBild></BildBox4>

<BackArrow onClick={scrollTop} style={{transform: showScroll?'translate(-50%)':'translate(-50%,300%)'}} /> 
</BildBoxWrap>



<Switch>
<Route path="/portrait"></Route>

<Route path="/animals"></Route>

<Route path="/art"></Route>

<Route path="/architecture"></Route>

</Switch>

</div>

    )
}

const Portfolio = () => {
    return ( 
        <Main></Main>
     );
}
 
export default Portfolio;