import styled from 'styled-components'
import { AiOutlineInstagram,AiOutlineFacebook,} from "react-icons/ai";
import {SiGmail } from "react-icons/si"
const Wrap = styled.div`
background-color: white;
height:60vh;
width:100%;
position:fixed;
top:20%;
box-shadow:0 5px 15px 10px black;
`

const InputStyle = styled.input`
top:20vh;
left:50%;
transform:translate(-50%);
position:absolute;
`
const ButtonStyle = styled.button`
color:white;
width:200px;
background-Color:pink;
border:black solid 2px;
borderRadius:50px;
top:25vh;
left:50%;
transform:translate(-50%);
position:absolute;
`
const Tytul = styled.h1`
top:0vh;
left:50%;
transform:translate(-50%);
position:absolute;
`
const Text = styled.h2`
top:30vh;
left:50%;
transform:translate(-50%);
position:absolute;
`

const WrapSocialMedia = styled.div`
top:50vh;
left:50%;
position:absolute;
transform:translate(-50%);
font-size:50px;
`

const Instagram = styled(AiOutlineInstagram)`

`
const Facebook = styled(AiOutlineFacebook)`

`
const Gmail = styled(SiGmail)`
color:DarkRed;
`

const Kontakt = () => {
    return ( <div style={{height:"100vh"}}>
<Wrap>
        <Tytul>Witam na mojej stronce!</Tytul>

        <InputStyle
         type="text"
        placeholder="E-mail@"
        />

    <ButtonStyle>Wyślij</ButtonStyle>

    <Text >Możesz zostawić tu swojego e-mail`a!</Text>
    <WrapSocialMedia><Instagram/> <Facebook/> <Gmail/></WrapSocialMedia>
    
    </Wrap>
    </div> );
}
 
export default Kontakt;