import styled from 'styled-components'
import React, {useEffect, useState} from 'react'
import bildKasia from '../Img/kasia.jpg'
import about from '../Img/About.jpg'
import ScrollToTop from '../Components/ScrollToTop';

const Wrapper = styled.div`
height:100vh;
width:100%;
display:flex;
align-items:center;
flex-direction: column;
flex-wrap:no-wrap;
justify-content:space-between;
`

const Bild = styled.img`
background-color:white;
border:2px solid black;
flex-basis:40px;
width:100%;
position:fixed;

`
const WrappText =styled.div`
z-index:20;
background-color:white;
position:absolute;
top:65%;
height:100vh;
width:100%;
margin:10px 50px;
padding:10px 50px;
text-align:center;

& p:first-letter {
    font-size:20px;
    
 & p.texto{
     font-size:50px;
 }   
}

`

const About = () => {
const [image, setImage]=useState([{
    src:bildKasia,
    id:1,
    active:false}])

useEffect(()=>{

    const TextShow=()=>{ 
    const textPath = document.querySelector('.texto')
 
textPath.getBoundingClientRect();
window.addEventListener("scroll",function(e){
    const scrollProcent=(document.documentElement.scrollTop+document.body.scrollTop)/(document.documentElement.scrollHeight-document.documentElement.clientHeight);
    if(scrollProcent<=0.40){
        textPath.style.display="none"
    } else {textPath.style.display=""}

    
    
})}
TextShow()

},[])

    return ( <Wrapper>
        <ScrollToTop/>
      {image.map(({src, id, active})=>
       
       <Bild src={src} key={id} id={id} alt=""/>)}
        <WrappText>
            <h1>Welcome!</h1>
            <p>
            Lorem ipsum, dolor sit amet consectetur adipisicing elit. Voluptate veniam reiciendis consequuntur praesentium facilis magnam pariatur quas, impedit beatae dignissimos deleniti ex et quaerat hic aliquid voluptas magni velit quod.</p>
            
            <p className="texto" id="texto">Lorem, ipsum dolor sit amet consectetur adipisicing elit. Culpa quam nihil, sed recusandae deleniti ab. Quis tenetur enim pariatur qui. Quidem architecto veniam odio optio quasi doloribus laudantium ducimus! Omnis?</p>
           
            </WrappText>
       
         </Wrapper> );
}
 
export default About;